

























































































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import CChobiGame from '@/components/links/CChobiGame.vue';
import CButton from '@/components/CButton.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';
import RpnCalc from '@/typescript/rpn/RpnCalc.ts';
import ListFormulaCalc from '@/typescript/rpn/ListFormulaCalc.ts';
import PermutationLister from '@/typescript/lister/PermutationLister.ts';
import MultiPermutationLister from '@/typescript/lister/MultiPermutationLister.ts';

@Component({
  components: {
    CChobiGame,
    CButton,
    CSelectorSample,
  },
  head: {
    title: {
      inner: '10パズル',
    },
  },
})
export default class TenPuzzle extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataButtonText: string = '解答';
  private dataFormulas: {
    rpn: string,
    in: string,
  }[] = [];
  private dataBaseNumber: string = '0000';
  private dataTargetNumber: string = '10';
  private dataCanExchange: boolean = true;
  private dataSamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: ',',
    },
    {
      name: 'question_00_01',
      caption: '0123（解なし）',
      content: '0123,10',
    },
    {
      name: 'question_01_01',
      caption: '1158（解1つ）',
      content: '1158,10',
    },
    {
      name: 'question_02_01',
      caption: '1116（解2つ、交換法則適用で1つ）',
      content: '1116,10',
    },
    {
      name: 'question_02_02',
      caption: '1167（解2つ、交換法則適用で1つ）',
      content: '1167,10',
    },
    {
      name: 'question_02_03',
      caption: '2289（解2つ、交換法則適用で1つ）',
      content: '2289,10',
    },
    {
      name: 'question_02_04',
      caption: '3478（解2つ、交換法則適用で1つ）',
      content: '3478,10',
    },
    {
      name: 'question_02_05',
      caption: '3577（解2つ、交換法則適用で1つ）',
      content: '3577,10',
    },
    {
      name: 'question_03_01',
      caption: '6779（解3つ、交換法則適用で2つ）',
      content: '6779,10',
    },
    {
      name: 'question_04_01',
      caption: '3466（4つの解、交換法則適用で1つ）',
      content: '3466,10',
    },
    {
      name: 'question_diff_01',
      caption: '2459（除算を使う解が1つ）',
      content: '2459,10',
    },
    {
      name: 'question_diff_02',
      caption: '5669（除算を2回使う解が2つ）',
      content: '5669,10',
    },
    {
      name: 'question_diff_03',
      caption: '3667（減算だけを使う解が6個）',
      content: '3667,10',
    },
    {
      name: 'question_121',
      caption: '5678（解121個）',
      content: '5678,10',
    },
    {
      name: 'question_154',
      caption: '4688（解154個）',
      content: '4688,10',
    },
  ];
  private dataSampleIndex: number = 0;
  private dataAnswer: string = '';

  // Lifecycle hook
  private created(): void {
    this.randamizeBaseNumber();
  }

  // Methods
  private randamizeBaseNumber(): void {
    this.dataBaseNumber = String(this.padZero(Math.floor(Math.random() * 10000), 4));
  }
  private listenOnSampleSelect($event: Event): void {
    let numbers: string[] = this.dataSamples[Number($event)].content.split(',');
    this.dataBaseNumber = numbers[0];
    this.dataTargetNumber = numbers[1];
  }
  private listenOnInputBaseNumber($event: Event): void {
    let baseNumber: string = (<HTMLInputElement>(<any>$event).target).value;
    if (baseNumber.match(/^[0-9]{4}$/g)) {
      this.dataBaseNumber = baseNumber;
    } else {
      this.dataBaseNumber = '';
    }
  }
  private listenOnCheckbox($event: Event): void {
    this.dataCanExchange = (<HTMLInputElement>(<any>$event).target).checked;
  }
  private listenOnInputTargetNumber($event: Event): void {
    let targetNumber: string = (<HTMLInputElement>(<any>$event).target).value;
    if (targetNumber.match(/^(\+|\-)?([1-9][0-9]+|[0-9])(\.[0-9]+)?$/)) {
      this.dataTargetNumber = targetNumber;
    } else {
      this.dataTargetNumber = '10';
    }
  }
  private solve(): void {
    this.dataFormulas.length = 0;
    let formulas: {
      rpn: string,
      in: string,
    }[] = [];
    if (this.dataBaseNumber !== '' && this.dataTargetNumber !== '') {
      let permutationLists: string[][] = this.dataCanExchange
        ? (new PermutationLister()).generateLists(
        this.dataBaseNumber.split(''),
        this.dataBaseNumber.length
        )
        : [this.dataBaseNumber.split('')]
      ;
      let operatorLists: string[][] = (new MultiPermutationLister()).generateLists(
        '+-*/'.split(''),
        this.dataBaseNumber.length - 1
      );
      let formulaLists: string[][] = ListFormulaCalc.splitEachFormula(
        ListFormulaCalc.rollTreeDigits(
          ListFormulaCalc.generateTreeDigits(this.dataBaseNumber.length)
        )
      );
      for (let i: number = 0; i < permutationLists.length; i++) {
        for (let j: number = 0; j < formulaLists.length; j++) {
          let sharpInsertedNumbers: string[] = [];
          for (let k: number = 0; k < formulaLists[j].length; k++) {
            if (formulaLists[j][k].match(/^[0-9]+$/)) {
              sharpInsertedNumbers = sharpInsertedNumbers.concat(
                permutationLists[i][Number(formulaLists[j][k])]
              );
            } else {
              sharpInsertedNumbers = sharpInsertedNumbers.concat(
                formulaLists[j][k]
              );
            }
          }
          for (let m: number = 0; m < operatorLists.length; m++) {
            let operatorReplacedNumbers: string[] = sharpInsertedNumbers.concat();
            let operatorList: string[] = operatorLists[m].concat();
            for (let n: number = 0; n < operatorReplacedNumbers.length; n++) {
              if (operatorReplacedNumbers[n] === '#') {
                operatorReplacedNumbers[n] = <string>operatorList.shift();
              }
            }
            if (
              RpnCalc.calc(operatorReplacedNumbers) === Number(this.dataTargetNumber)
              && RpnCalc.errorMessage === ''
            ) {
              formulas.push({
                rpn: operatorReplacedNumbers.join(' '),
                in: RpnCalc.convertFromRpn(operatorReplacedNumbers),
              });
            }
          }
        }
      }
    }
    this.dataFormulas = formulas;
  }
  private padZero(number: number, length: number): string {
    return ('0'.repeat(length) + String(number)).slice(-length);
  }
}
